import React, { useState } from 'react';
import { Box, Modal } from '@mui/material';
import { postBack } from '../api/BackAPI';
import { modalStyle } from '../Util.js';

// TODO: Upload Input   , : https://mui.com/material-ui/react-button/#upload-button
// TODO:  CSS 
// TODO: Creation Modal CSS 
// TODO: Child Modal CSS 

function CreationModal(props) {
    const [image, setImage] = useState(null);
    const [backId, setBackId] = useState('')
    const [joinModalOpen, setJoinModalOpen] = useState(false);

    const handleImageChange = (e) => {
        console.log(e.target.files[0])
        setImage(e.target.files[0])
    };

    const handleSubmit = () => {
        postBack(image)
            .then(response => {
                console.log(response.data);
                setBackId(response.data.id)
                setJoinModalOpen(true)
            })
            .catch(error => {
                console.log(error)
                alert('  .')
            })
    };

    const handleJoinModalClose = () => {
        setJoinModalOpen(false)
        props.onClose()
    };

    return (
        <Modal
            open={props.open}
            onClose={props.onClose}
        >
            <Box sx={modalStyle}>
                <input type="file"
                       id="image"
                       accept=".png, .jpeg, .jpg" onChange={handleImageChange} required/>
                <button onClick={handleSubmit} disabled={ image === null }></button>
                <button onClick={props.onClose}></button>
                <Modal
                    hideBackdrop
                    open={joinModalOpen}
                    onClose={handleJoinModalClose}
                    aria-describedby='child-modal-description'
                >
                    <Box sx={{ ...modalStyle, width: 200 }}>
                        <p id='child-modal-description'>
                               {backId}.  ?
                        </p>
                        <button onClick={() => { props.moveToBackViewer(backId) }}></button>
                        <button onClick={handleJoinModalClose}></button>
                    </Box>
                </Modal>
            </Box>
        </Modal>
    )
}

export default CreationModal;
